<template>
    <v-container>
        <v-layout>
            <v-card height="70"></v-card>
        </v-layout>
        <v-layout>
            <v-flex 10>
                <my_card :box="true" :chart_data="data" :chart_setting="setting"></my_card>
            </v-flex>
        </v-layout>
        <v-btn
                absolute
                dark
                fab
                bottom
                right
                color="blue"
                @click="change()"
        >
            <v-icon>arrow_forward_ios</v-icon>
        </v-btn>
    </v-container>
</template>

<script>
    // @ is an alias to /src
    import my_card from "../components/my_card";
    export default {
        name: 'home_hist',
        components: {
            my_card
        },
        mounted() {
            this.axios.get('http://0.0.0.0:3305/data_table/box_' + this.val).then(response =>
                (
                    this.data.rows = response.data
                )
            )
        },
        data() {
            return {
                val:1,
                setting: {

                    legendName: {
                        '日K': 'kanji'
                    },
                    showDataZoom: true,
                    start: 0,
                    end: 100
                },
                data: {
                    columns: ['strokes', 'open', 'close', "highest", "lowest"],
                    rows: [],
                }


            }
        },
        methods: {
            change() {
                this.val += 1
                if (this.val > 3) this.val = 1;
                this.axios.get('http://0.0.0.0:3305/data_table/box_' + this.val).then(response =>
                    (
                        this.data.rows = response.data
                    )
                )
            }
        }
    }
</script>
