<template>
    <div>
        <v-navigation-drawer
                v-model="drawer"
                style="background: linear-gradient(to bottom, #00416A,#E4E5E6)"
                dark
                clipped
                app
        >
            <v-list>
                <v-list-item
                        v-for="([icon, text, link], i) in items"
                        :key="i"
                        :to="{path: link}"
                        link
                >
                    <v-list-item-icon>
                        <v-icon>{{ icon }}</v-icon>
                    </v-list-item-icon>

                    <v-list-item-content>
                        <v-list-item-title>{{ text }}</v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
            </v-list>
        </v-navigation-drawer>
        <v-app-bar
                style="background: linear-gradient(to right,#00416A, #E4E5E6)"
                class="mr-5 align-center"
                clipped-left
                dark
                app
        >
            <v-app-bar-nav-icon
                    @click.stop="drawer = !drawer"
            >
            </v-app-bar-nav-icon>

            <v-icon>filter_hdr</v-icon>
            <v-toolbar-title>  Kanji</v-toolbar-title>

            <v-spacer/>
            <v-spacer/>
            <v-spacer/>
            <v-spacer/>
            <v-spacer/>


            <v-autocomplete
                    placeholder="Search..."
                    v-model="value"
                    :items="searchs"
                    single-line
                    color="white"
                    hide-details
                    filled
                    rounded
            />
                <v-btn
                       color="white"
                       icon
                       @click="redirect(value)"
                >
                    <v-icon>search</v-icon>
                </v-btn>

        </v-app-bar>
        <v-overlay :value="dialog" opacity="0.8">
        <v-dialog v-model="dialog" hide-overlay transition="out-in">
            <v-card>
                <v-toolbar dense dark color="white">
                    <v-spacer></v-spacer>
                    <v-btn icon color="black" @click="dialog = false">
                        <v-icon>mdi-close</v-icon>
                    </v-btn>
                </v-toolbar>
                <kanji  :char="single.name"
                        :level="single.levels"
                        :meaning="single.meaning"
                        :kun="single.kun"
                        :on="single.on"
                ></kanji>
            </v-card>
        </v-dialog>
        </v-overlay>
    </div>
</template>

<script>

    import kanji from "./kanji";
    export default {
        name: "my_app_bar",
        components: {
            kanji
        },
        mounted () {
            this.axios.get('http://0.0.0.0:3305/data_table/kanjis').then(response =>
                (
                    this.searchs = response.data

                )
            );
            },
        data() {
            return {
                single: {},
                dialog: false,
                items: [
                    // ['timeline', 'Line', "/"],
                    // ['scatter_plot', 'Scatter', "scatter"],
                    // ['assessment', 'His', "hist"],
                    ['assessment', 'Box', "/"],
                    ['border_all', 'kanji', "kanji"],
                ],
                searchs: [],
                drawer: true,
                value: null,
            }
        },
        methods: {
            redirect(value) {
                if (!value) {
                    return
                }
                this.axios.put('http://0.0.0.0:3305/data_table/desc/'+ value).then(response =>
                    (
                        this.single = response.data

                    )
                );
                this.dialog = true
            },
        }
    }
</script>

