<template>
    <div>
    <v-card hover>
    <v-data-table
            :headers="headers"
            :items="kanjis"
            class="elevation-1"
            :sort-by="['levels']"
            :sort-desc="[true]"
    >
        <template v-slot:item.levels="{ item }">
            <v-rating
                    :value="item.levels"
                    color="purple"
                    dense
                    half-increments
                    readonly
                    size="14"
            ></v-rating>
        </template>
        <template v-slot:item.detail="{ item }">
            <v-btn icon fab>
            <v-icon
                    small
                    class="mr-2"
                    @click="open_detail(item)"
            >
                launch
            </v-icon>
            </v-btn>
        </template>
    </v-data-table>
    </v-card>
        <v-overlay :value="dialog" opacity="0.8">
            <v-dialog v-model="dialog" hide-overlay transition="out-in"  :loading="load"
            >
                <v-card>
                    <v-toolbar dense dark color="white">
                        <v-spacer></v-spacer>
                        <v-btn icon color="black" @click="dialog = false">
                            <v-icon>mdi-close</v-icon>
                        </v-btn>
                    </v-toolbar>
                    <kanji  :char="single.name"
                            :level="single.levels"
                            :meaning="single.meaning"
                            :kun="single.kun"
                            :on="single.on"
                    ></kanji>
                </v-card>
            </v-dialog>
        </v-overlay>
    </div>
</template>

<script>
    import kanji from "./kanji";
    export default {
        components: {
            kanji
        },
        mounted() {
            this.axios.get('http://0.0.0.0:3305/data_table').then(response =>
                (
                    this.kanjis = response.data

                )
            )
        },

        data() {
            return {
                single: {},
                dialog: false,
                load: false,
                headers: [
                    {
                        text: 'Kanji',
                        align: 'left',
                        sortable: false,
                        value: 'name',
                    },
                    {text: 'Hard level', value: 'levels'},
                    {text: 'Frequency', value: 'frequency'},
                    {text: 'Strokes', value: 'strokes'},
                    {text: 'Detail', value: "detail"},
                ],
                kanjis: [],
            }

        },
        methods: {
            open_detail(item) {
                this.load = true;
                this.axios.put('http://0.0.0.0:3305/data_table/desc/'+ item.name).then(response =>
                    (
                        this.single = response.data

                    )
                );
                this.load = false;
                this.dialog = true
            }
        }
    }
</script>
