import Vue from 'vue'
import VueRouter from 'vue-router'
import home_line from '../views/Home_line.vue'
import home_scatter from '../views/Home_scatter.vue'
import home_hist from '../views/Home_hist.vue'
import kanji_page from "../views/kanji_page";
import home_box from "../views/Home_box"

Vue.use(VueRouter);

const routes = [
  {
    path: '/home',
    name: 'home_line',
    component: home_line
  },
  {
    path: '/scatter',
    name: 'home_scatter',
    component: home_scatter
  },
  {
    path: '/hist',
    name: 'home_hist',
    component: home_hist
  },
  {
    path: '/',
    name: 'Home_box',
    component: home_box
  },
  {
    path: '/kanji',
    name: 'kanji_page',
    component: kanji_page
  },

  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  }
]

const router = new VueRouter({
  routes
})

export default router
